"use client";

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import Perview from "@/app/preview/page";

import {
  Upload,
  X,
  Plus,
  GripVertical,
  Trash2,
} from "lucide-react";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { DashboardLayout } from "@/components/dashboard/layout";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import axiosClient from "@/lib/axiosClient";
import { showToast } from "@/lib/showToast";
import { usePageStore } from "@/lib/pageStore";
import dynamic from "next/dynamic";

const Builder = dynamic(() => import("@/app/landing-pages/new/page.client"), {
  ssr: false,
});

interface ImageFile {
  id: string;
  url: string;
  file?: File;
}

interface Section {
  id: number;
  key: string;
  name: string;
  group: string;
  sub_group: string | null;
  description: string | null;
  schema: {
    component: string;
    fields: Array<{
      key: string;
      label: string;
      type: string;
      required?: boolean;
      multiple?: boolean;
      analytics?: boolean;
      tempId?: string;
      subFields?: Array<{
        key: string;
        label: string;
        type: string;
        required?: boolean;
      }>;
    }>;
  };
  is_active: boolean;
  sort_order: number;
}

export default function NewProductPage() {
  const { templateId, userId } = usePageStore();
  const [productName, setProductName] = React.useState("iPhone 15 Pro Max");
  const [category, setCategory] = React.useState<any[]>([]);
  const [sku, setSku] = React.useState("IPH15PRO");
  const [price, setPrice] = React.useState("999");

  const [tags, setTags] = React.useState<string[]>([]);
  const [newTag, setNewTag] = React.useState("");

  const [metaTitle, setMetaTitle] = React.useState("");
  const [metaDescription, setMetaDescription] = React.useState("");
  const [keywords, setKeywords] = React.useState("");

  const [sectionsData, setSectionsData] = React.useState<Record<string, any>>({});
  const [activeTab, setActiveTab] = React.useState("details");
  const [isPublishing, setIsPublishing] = React.useState(false);
  const [publishStatus, setPublishStatus] = React.useState<"idle" | "success" | "error" | null>(null);
  const [isMobilePreviewVisible, setIsMobilePreviewVisible] = React.useState(true);
  const [selectedCategory, setSelectedCategory] = React.useState("");
  const [isCustomizeModalOpen, setIsCustomizeModalOpen] = React.useState(false);

  const [sections, setSections] = React.useState<Section[]>([]);
  const [loadingSections, setLoadingSections] = React.useState(true);

  const updateField = (sectionKey: string, fieldKey: string, value: any) => {
    setSectionsData(prev => ({
      ...prev,
      [sectionKey]: { ...prev[sectionKey], [fieldKey]: value },
    }));
  };

  const renderCategoryOptions = (
    items: any[],
    level = 0
  ): React.ReactNode[] => {
    return items.flatMap((cat, index) => {
      const uniqueKey = `${cat.id}-${level}-${index}`;
      const current = (
        <option key={uniqueKey} value={cat.id}>
          {"".repeat(level)} {cat.name}
        </option>
      );
      const children = cat.children?.length
        ? renderCategoryOptions(cat.children, level + 1)
        : [];
      return [current, ...children];
    });
  };

  const loadCategories = async () => {
    try {
      const res = await axiosClient.get("/product-categories");
      if (res.data.success) setCategory(res.data.data || []);
    } catch (err: any) {
      showToast(err.response?.data?.message || "Failed to load categories", "error");
    }
  };

  const loadSections = async () => {
    setLoadingSections(true);
    try {
      const res = await axiosClient.get("/landing-page/sections");

      // Exactly matches your API response
      const contentObj = res.data.content || {};
      const emptyKeyArray = contentObj[""] || []; // this is your array of sections!

      if (Array.isArray(emptyKeyArray) && emptyKeyArray.length > 0) {
        setSections(emptyKeyArray);
        showToast(`Loaded ${emptyKeyArray.length} beautiful sections!`, "success");
      } else {
        showToast("No sections found  please add in admin", "warning");
      }
    } catch (err: any) {
      showToast("Cannot reach sections  check internet", "error");
      console.error(err);
    } finally {
      setLoadingSections(false);
    }
  };

  const addTag = () => {
    const t = newTag.trim();
    if (t && !tags.includes(t)) {
      setTags([...tags, t]);
      setNewTag("");
    }
  };

  const buildFinalJson = () => {
    const activeSections = sections
      .filter(sec => sec.is_active)
      .sort((a, b) => a.sort_order - b.sort_order)
      .map(sec => {
        const data = sectionsData[sec.key] || {};
        const fields: any[] = [];

        sec.schema.fields.forEach(field => {
          const value = data[field.key];
          if (!value || value === "" || (Array.isArray(value) && value.length === 0)) return;

          if (field.type === "image") {
            if (Array.isArray(value)) {
              value.forEach((img: ImageFile) => {
                if (img.url) fields.push({ name: field.key, url: img.url });
              });
            } else if (value?.url) {
              fields.push({ name: field.key, url: value.url });
            }
          } else if (field.type === "repeater") {
            if (Array.isArray(value) && value.length > 0) {
              fields.push({ name: field.key, items: value });
            }
          } else {
            fields.push({ name: field.key, value: String(value) });
          }
        });

        return fields.length > 0 ? { section: sec.key, fields } : null;
      })
      .filter(Boolean);

    return {
      user_id: 1,
      templateName: "modern",
      product_id: Date.now(),
      name: productName || "New Product",
      category_id: selectedCategory || "Uncategorized",
      sku: sku || "N/A",
      price: price || "0",
      tag: tags,
      meta_title: metaTitle || "",
      meta_description: metaDescription || "",
      keywords: keywords || "",
      sections: activeSections,
    };
  };

  const liveData = React.useMemo(() => buildFinalJson(), [
    productName, selectedCategory, sku, price, tags,
    metaTitle, metaDescription, keywords, sectionsData, sections
  ]);

  React.useEffect(() => {
    loadCategories();
    loadSections();
  }, []);

  const handlePublish = async () => {
    const payload = buildFinalJson();
    setIsPublishing(true);
    setPublishStatus(null);

    try {
      const response = await axiosClient.post("/products", payload);
      showToast("Product published successfully!", "success");
      setPublishStatus("success");
      if (response.data?.id) showToast(`Product ID: ${response.data.id}`, "info");
    } catch (error: any) {
      setPublishStatus("error");
      showToast(error.response?.data?.message || "Failed to publish", "error");
    } finally {
      setIsPublishing(false);
      setTimeout(() => setPublishStatus(null), 5000);
    }
  };

  return (
    <DashboardLayout>
      <div className="min-h-screen bg-gray-50">
        {/* Header same as before  I kept it short here */}
        <div className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-6 py-6 flex justify-between items-center">
            <h1 className="text-3xl font-bold">Create Product Landing Page</h1>
            <div className="flex gap-4">
              <Button onClick={() => setIsCustomizeModalOpen(true)}>Customize</Button>
              <Button variant="outline" onClick={() => setIsMobilePreviewVisible(!isMobilePreviewVisible)}>
                {isMobilePreviewVisible ? "Hide Preview" : "Show Preview"}
              </Button>
              <Button
                disabled={isPublishing}
                className={isPublishing ? "bg-gray-500" : "bg-gradient-to-r from-blue-600 to-indigo-600"}
                onClick={handlePublish}
              >
                {isPublishing ? "Publishing..." : "Publish Live"}
              </Button>
            </div>
          </div>
        </div>

        <div className="w-full mx-auto">
          <div className={`grid grid-cols-1 gap-10 ${isMobilePreviewVisible ? "lg:grid-cols-2" : "grid-cols-1"}`}>
            <div className="space-y-8">
              <Tabs value={activeTab} onValueChange={setActiveTab}>
                <TabsList className="grid grid-cols-3 w-full">
                  <TabsTrigger value="details">Product Details</TabsTrigger>
                  <TabsTrigger value="sections">Sections</TabsTrigger>
                  <TabsTrigger value="seo">SEO</TabsTrigger>
                </TabsList>

                {/* Product Details Tab  same */}
                <TabsContent value="details">{/* your details fields here  unchanged */}</TabsContent>

                <TabsContent value="sections" className="mt-6">
                  {loadingSections ? (
                    <div className="text-center p-10">
                      <div className="animate-spin h-10 w-10 mx-auto border-4 border-indigo-600 rounded-full border-t-transparent"></div>
                      <p className="mt-4 text-lg">Loading your sections...</p>
                    </div>
                  ) : sections.length === 0 ? (
                    <div className="text-center p-10 text-gray-500">
                      No sections yet  add them in admin panel
                    </div>
                  ) : (
                    <Accordion type="single" collapsible className="space-y-4">
                      {sections
                        .filter(sec => sec.is_active)
                        .sort((a, b) => a.sort_order - b.sort_order)
                        .map((sec, index, array) => (
                          <AccordionItem key={sec.key} value={sec.key}>
                            <AccordionTrigger className="px-6 py-5 text-lg font-bold bg-gray-50">
                              <div className="flex items-center gap-3">
                                <GripVertical className="h-5 w-5" />
                                {sec.name} ({sec.key})
                              </div>
                            </AccordionTrigger>
                            <AccordionContent className="px-6 pt-6 pb-8">
                              <div className="space-y-8">
                                {sec.schema.fields.map(field => {
                                  const value = sectionsData[sec.key]?.[field.key];

                                  if (field.type === "image") {
                                    const imgs = Array.isArray(value) ? value : value ? [value] : [];
                                    return (
                                      <div key={field.key} className="space-y-3">
                                        <Label>{field.label} {field.required && <span className="text-red-500">*</span>}</Label>
                                        <div className="grid grid-cols-3 gap-4">
                                          {imgs.map((img: ImageFile) => (
                                            <div key={img.id} className="relative">
                                              <img src={img.url} className="w-full h-32 object-cover rounded" />
                                              <button onClick={() => updateField(sec.key, field.key, imgs.filter(i => i.id !== img.id))}>
                                                <X className="absolute top-1 right-1 bg-red-600 text-white rounded-full p-1" />
                                              </button>
                                            </div>
                                          ))}
                                          <label className="h-32 border-2 border-dashed flex items-center justify-center cursor-pointer">
                                            <Upload />
                                            <input
                                              type="file"
                                              multiple
                                              className="hidden"
                                              onChange={e => {
                                                const newFiles = Array.from(e.target.files || []).map(file => ({
                                                  id: Date.now().toString(),
                                                  url: URL.createObjectURL(file),
                                                  file
                                                }));
                                                updateField(sec.key, field.key, [...imgs, ...newFiles]);
                                              }}
                                            />
                                          </label>
                                        </div>
                                      </div>
                                    );
                                  }

                                  if (field.type === "repeater") {
                                    // repeater logic if you add later
                                    return <div key={field.key}>Repeater coming soon</div>;
                                  }

                                  return (
                                    <div key={field.key} className="space-y-2">
                                      <Label>{field.label} {field.required && <span className="text-red-500">*</span>}</Label>
                                      {field.type === "textarea" ? (
                                        <Textarea
                                          value={value || ""}
                                          onChange={e => updateField(sec.key, field.key, e.target.value)}
                                          rows={4}
                                        />
                                      ) : (
                                        <Input
                                          type={field.type === "url" ? "url" : field.type}
                                          value={value || ""}
                                          onChange={e => updateField(sec.key, field.key, e.target.value)}
                                        />
                                      )}
                                    </div>
                                  );
                                })}
                              </div>
                            </AccordionContent>
                          </AccordionItem>
                        ))}
                    </Accordion>
                  )}
                </TabsContent>

                {/* SEO Tab same */}
              </Tabs>
            </div>

            {isMobilePreviewVisible && (
              <div className="sticky top-0 py-8">
                <div className="w-[360px] h-[720px] mx-auto border-8 border-black rounded-3xl overflow-hidden shadow-2xl">
                  <Perview data={liveData} />
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Customize Modal same */}
      </div>
    </DashboardLayout>
  );
}